.header {
  background: #2aacb8;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

@media (max-width: 700px) {
  .nav {
    background: red;
    transition: all .5s;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    transform: translateX(-100vw);
  }

  .nav--show {
    transform: translateX(0);
  }

  .nav__button {
    text-align: right;
    margin: 1rem;
  }

  .nav__ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  .nav__link {
    text-align: center;
    color: #000;
    padding: 1rem;
    font-size: 1.5rem;
    text-decoration: none;
    display: block;
  }
}

@media (min-width: 701px) {
  .nav__button, .header__button-nav--open {
    display: none;
  }

  .nav__ul {
    padding: 0;
    list-style: none;
    display: flex;
  }

  .nav__link {
    color: #000;
    padding: .5rem 1rem;
    text-decoration: none;
    transition: all .5s;
    display: block;
  }

  .nav__link:hover {
    opacity: .5;
  }
}

